import "Eyes.css"

export default class Eyes extends React.Component {
    render() {
    	let {className, click} = this.props
        return <svg x="0px" y="0px" viewBox="0 0 69 172" className={`eyes ${className}`} onClick = {click}>
        <path d="M34.8,1c0,0-73.8,73.8,0,170"/>
        <path d="M35,1c0,0,73.8,73.8,0,170"/>
        <circle cx="34.5" cy="86" r="12.3"/>
    </svg>
    }
}


