import 'VerseListItem.css'

export default class VerseListItem extends React.Component {

	constructor(p){
		super(p)
	}

    render() {

        let {item: {id, name, content}, onMouseOver, onMouseLeave }= this.props

        return  <div className="verse_items" data-id={`${id}`} onMouseOver={onMouseOver} onMouseLeave={onMouseLeave} >
        			<NavLink to={`/verse/${id}`}>
        				<h2>{name}</h2>
        			</NavLink>
        		</div>
    }
}


