import * as types from 'uiAction'

const initialState = {
    preloadDone: false,
    verseNum: null
}

export default (state = initialState, action) => {
    switch (action.type) {
        case types.SET_PRELOAD_DONE:
            return {
                ...state,
                preloadDone: action.preloadDone
            }

        case types.SET_VERSENAME:
            return {
                ...state,
                verseNum: action.verseNum
            }

        default:
            return state
    }
}


