import 'Arrow.css'

export default class Arrow extends React.Component {
    render() {
    	let {className} = this.props
        return <svg version="1.1" x="0px" y="0px"
		 viewBox="0 0 9.3 13" className={`arrow ${className}`}>
	<path d="M7.5,12.3L1.7,6.5l5.8-5.8"/>
	</svg>
    }
}


