export const SET_PRELOAD_DONE = 'SET_PRELOAD_DONE'

export const setPreloadDone = (preloadDone) => {
    return {
        type: SET_PRELOAD_DONE,
        preloadDone
    }
}

export const SET_VERSENUM = 'SET_VERSENUM'

export const setVerseNum = (verseNum) => {
    return {
        type: SET_VERSENUM,
        verseNum
    }
}


