import "AboutPage.css"
import Arrow from 'Arrow'
import {TweenLite} from 'gsap'
import ScrollToPlugin from 'gsap/ScrollToPlugin';


export default class AboutPage extends React.Component {
    constructor(p){
        super(p)
        this.contact
        this.scrollTopBtn
        this.state = {
            scrollText: 'SCROLL DOWN',
            scrollAnimation: false,
            arrowAnimation: false,
        }
    }
    disableScrollDetection = () =>{
        this.isScrolling = true
        setTimeout( () => {
            this.isScrolling = false
        }, 1000 )
    }
    changeScrollText = () =>{
        let { scrollTopBtn } = this
        let body = document.body
        let bodyHeight = Math.ceil(body.getBoundingClientRect().height)
        let scrollHeight = Math.ceil(window.scrollY + window.innerHeight)
        if( scrollHeight == bodyHeight ){
            this.setState({...this.state, scrollAnimation: !this.state.scrollAnimation, scrollDisable: !this.state.scrollDisable})
            setTimeout(function(){
                 this.setState({...this.state, scrollText: 'SCROLL TOP', arrowAnimation: !this.state.arrowAnimation})
             }.bind(this), 500)
           
        }else{
            this.setState({...this.state, scrollAnimation: false, scrollDisable: false, scrollText: 'SCROLL DOWN', arrowAnimation: false, scrollAnimation: false})
        }
    }
    scrollTop = () =>{
        TweenLite.to(window, .5, {scrollTo:0});
    }
    componentDidMount(){
        let { contact, scrollTopBtn } = this
        window.addEventListener('scroll', this.changeScrollText)
        scrollTopBtn.addEventListener('click', this.scrollTop)
    }

    render() {
        let {scrollText, scrollAnimation, arrowAnimation} = this.state
        return <section className='page about_page'>
        	<div className="foreward">
                <p>
                    宗教，一個名詞，包含了許多人心裏寄託的某一個方式，僅僅是教人認識自我，從善從良的精神，卻使人因此歧視彼此，我們認為好的經典不管是哪種形式，都是值得人閱讀及傳播。
                </p>
            </div>
            <div className="about_content">
                <div className="illustraion">
                    <div className="why">
                        <p>
                            宗教，一個名詞，包含了許多人心裏寄託的某一個方式，從善從良的精神，卻使人因此歧視彼此，我們認為好的經典不管是哪種形式，都是值得人閱讀及傳播。
                        </p>
                        <p>
                            每個人都有屬於自己的路，始終存在，也許您已經在路上了，或者尚未經過，但路依舊在那，這本書也許將是您的其中一個路口。
                        </p>
                        
                    </div>
                    <div className="what">
                        <p>
                            一切有為法如夢幻泡影，如露亦如電，應作如是觀。
                        </p>
                    </div>
                    <div className="contact" ref={ el => this.contact = el }>
                        <span>diamondsutra2019@gmail.com</span>
                        <span>Copyright © 2019 DiamondSutra. All rights reserved</span>
                    </div>
                </div>
                <div className="pic">
                    <img src={`${process.env.PUBLIC_PATH}/asset/about.png`}/>
                </div>
                <div className="contact mobile">
                    <span>diamondsutra2019@gmail.com</span>
                    <span>Copyright © 2019 DiamondSutra.</span>
                </div>
            </div>
            <div className="scrolldown">
                <Arrow className={`${ arrowAnimation || 'dn' }`}/>
                <span ref={el => this.scrollTopBtn = el}>{scrollText}</span>
                <div className={`effectLine ${ scrollAnimation && 'switchAnimation'}`}></div>
                <Arrow className={`${ arrowAnimation && 'dn' }`}/>
            </div>
        </section>
    }
}


