export default class Trangle extends React.Component {
    render() {
    	let {onclick, classname} = this.props
        return <svg viewBox="0 0 496.17 434" className={classname} onClick={onclick}>
        	<polygon points="252.33 434 0.13 0 496.13 0 252.33 434"/>
        </svg>
    }
}


