import "VersePage.css"
import Arrow from 'Arrow'
import verseData from 'verseData'
import DragEvent from 'DragEvent'

let getVerseData = (id) => {
    let filtered = verseData.filter(e => {return e.id == id})
    return filtered[0]
}

export default class VersePage extends React.Component {
    constructor(p){
        super(p)
        this.state = {
            nextData: null,
            prevData: null,
            data: null,
            verseNum: null
        }
    }

    componentWillMount(){
        let {match:{params}} = this.props

        if( params.id == verseData.length ){
            let nowData = getVerseData(params.id)
            let nextData = getVerseData(1)
            let prevData = getVerseData(Number(params.id) - 1)
            let nowNum = params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
        else if( params.id == 1 ){
            let nowData = getVerseData(params.id)
            let nextData = getVerseData(Number(params.id) + 1)
            let prevData = getVerseData(verseData.length)
            let nowNum = params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
        else{
            let nowData = getVerseData(params.id)
            let nextData = getVerseData(Number(params.id) + 1)
            let prevData = getVerseData(Number(params.id) - 1)
            let nowNum = params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
    }

    componentDidMount(){
        let {match:{params}} = this.props

        if( params.id == verseData.length ){
            let nowData = getVerseData(params.id)
            let nextData = getVerseData(1)
            let prevData = getVerseData(Number(params.id) - 1)
            let nowNum = params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
        else if( params.id == 1 ){
            let nowData = getVerseData(params.id)
            let nextData = getVerseData(Number(params.id) + 1)
            let prevData = getVerseData(verseData.length)
            let nowNum = params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
        else{
            let nowData = getVerseData(params.id)
            let nextData = getVerseData(Number(params.id) + 1)
            let prevData = getVerseData(Number(params.id) - 1)
            let nowNum = params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }

        console.log(this.state.verseNum)
    }

    componentWillReceiveProps(np){
        let {match:{params}} = this.props

        if( params.id !== verseData.length && np.match.params.id == verseData.length){
            let nowData = getVerseData(verseData.length)
            let prevData = getVerseData((np.match.params.id) - 1)
            let nextData = getVerseData(1)
            let nowNum = np.match.params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
        else if( params.id !== 1 && np.match.params.id == 1 ){
            let nowData = getVerseData(1)
            let prevData = getVerseData(verseData.length)
            let nextData = getVerseData(Number(np.match.params.id) + 1)
            let nowNum = np.match.params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})

        }
        else if( params.id !== np.match.params.id ){
            let nowData = getVerseData(np.match.params.id)
            let nextData = getVerseData(Number(np.match.params.id) + 1)
            let prevData = getVerseData(Number(np.match.params.id) - 1)
            let nowNum = np.match.params.id
            this.setState({...this.state, nextData: nextData, prevData: prevData, data: nowData, verseNum: nowNum})
        }
    }
    render() {
        let { name, content, id } = this.state.data
        let { verseNum } = this.state
        let doneDragArray = [1, 2, 12, 14, 15, 16, 17, 18, 20, 21, 23, 24, 27]
        let done = doneDragArray.includes(id)
        let doneNum = doneDragArray.filter((e) => {return e == id})
        return <div className='page fullh'>
            {
                done ? <DragEvent verseNum={ doneNum[0] } /> : ''

            }
        	<section className="verse_page">
        		<div className="content">
                    <Link to='/'>
                        <div className="back">
                            <Arrow/>
                            <span>BACK</span>
                        </div>
                    </Link>
                    <div className="title">
                        <span>{name}</span>
                    </div>
                    <div className="explaintion" dangerouslySetInnerHTML={{__html: content}}/>
                    <div className="slideoption">
                        <Link to={`${this.state.prevData.id}`}>
                            <div className="prev">
                                <Arrow/>
                            </div>
                        </Link>
                        <div className="num">
                            <span>{id} / 40</span>
                        </div>
                        <Link to={`${this.state.nextData.id}`}>
                            <div className="next">
                                <Arrow/>
                            </div>
                        </Link>
                    </div>
                </div>
                <div className="pic">
                    <img src={`${process.env.PUBLIC_PATH}/asset/${id}.jpg`}/>
                </div>
        	</section>
        </div>
    }
}


