import "Header.css"
import { isMobile } from 'globals'



export default class Header extends React.Component {
    constructor(p){
        super(p)
        this.header
        this.state = {
            menuVisible: false
        }
        this.scrollDown = this.scrollDown.bind(this)
    }
    scrollDown = () => {
      window.scrollY > 0 ? this.header.classList.add('scroll') : this.header.classList.remove('scroll')
    }
    setMenuVisible(){
        if(isMobile){
          this.setState({ menuVisible: !this.state.menuVisible });
        }
        
    }
    componentDidMount(){
      window.addEventListener('scroll', this.scrollDown)
    }
    render() {
        let {menuVisible} = this.state
        return <header className='header' ref={ el => this.header = el }>
            <div className="container">
                <div className="logo">
                        <img src={`${process.env.PUBLIC_PATH}/asset/logo.svg`}/>
                    </div>
                <div className={`hamburger ${menuVisible &&' visible'}`}  onClick = { () => this.setMenuVisible(!menuVisible) }>
                    <span></span>
                </div>
            </div>
            <ul className={`menu ${menuVisible && 'visible'}`}>
               <li>
                   <NavLink to='/about' onClick = { () => this.setMenuVisible(!menuVisible) }>
                       <span>關於</span>
                   </NavLink>
               </li>
               <li>
                   <NavLink to='/' onClick = { () => this.setMenuVisible(!menuVisible) }>
                       <span>偈子</span>
                   </NavLink>
               </li>
               {/*<li>
                   <NavLink to='/devotion' onClick = { () => this.setMenuVisible(!menuVisible) }>
                       <span>體悟</span>
                   </NavLink>
               </li>*/}
            </ul>
        </header>
    }
}


